.wx_loading {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0);
}

.wx_loading .wx_loading_inner {
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  color: #ffffff;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -1.49333rem;
  margin-top: -1.024rem;
  width: 3.2rem;
  border-radius: 0.128rem;
  font-size: 0.29867rem;
  padding: 1.23733rem 0 0.21333rem 0;
}

.wx_loading .wx_loading_inner .wx_loading_icon {
  position: absolute;
  top: 0.32rem;
  left: 50%;
  margin-left: -0.34133rem;
  width: 0.512rem;
  height: 0.512rem;
  border: 0.04267rem solid #fff;
  border-radius: 0.512rem;
  -webkit-animation: gif 1s infinite linear;
          animation: gif 1s infinite linear;
  clip: rect(0 auto 0.256rem 0);
}

@-webkit-keyframes gif {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes gif {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

